import {Federated} from '@callstack/repack/client';
import React from 'react';
import SplashScreen from '../../components/Splash';
import ErrorBoundary from '../../components/ErrorBoudary';
import {useNavigation} from '@react-navigation/native';
const EContractScreenCM = React.lazy(() =>
  Federated.importModule('eContract', './createContract'),
);

const CreateContractScreen = () => {
  const navigation = useNavigation();
  return (
    <ErrorBoundary name="" navigation={navigation}>
      <React.Suspense fallback={<SplashScreen app="Create contract screen" />}>
        <EContractScreenCM />
      </React.Suspense>
    </ErrorBoundary>
  );
};

export default CreateContractScreen;
